.App {
  text-align: center;
}

.App-header {
  background-color: #222;
  height: 50px;
  padding: 20px;
  color: white;
}

.App-title {
  font-size: 1.5em;
}

.menu-l {
  display: block;
  position: relative;
  margin: 0;
  padding: 0;
  background-color: #ddd;
  width: 100%;
  z-index: 200;
}

.menu-i {
  display: inline-block;
  padding: 15px;
}


